  display: flex;
  overflow: auto;
  padding: 5rem 0;
  width: 100%;
  background: white; }
  .page-navigation.sticky {
    position: fixed;
    top: 50px;
    z-index: 2;
    padding: 2rem 0;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15); }
    .page-navigation.sticky + section {
