.hero {
  background: #213D4B 50% 25% / cover no-repeat;
  overflow: hidden;
  position: relative; }
  .hero::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    display: block;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #213d4b;
    z-index: 0;
    opacity: 0.5;
    mix-blend-mode: multiply; }
  .hero .container {
    position: relative;
    z-index: 1; }
  .hero .container .column {
    margin: 10rem auto 2.5vw auto; }
    @media (min-width: 600px) {
      .hero .container .column {
        margin: 15vw auto 2.5vw auto; } }
    @media (min-width: 1000px) {
      .hero .container .column {
        margin: 15rem auto 3rem auto; } }
    @media (max-width: 949px) {
      .hero .container .column h1 {
        font-size: 16vw;
        margin-bottom: 0.1em; } }

.uppercase {
  text-transform: uppercase; }

.column.has-sizer {
  background: 50% 50% / cover no-repeat;
  position: relative;
  display: block; }
  .column.has-sizer:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(111, 201, 193, 0.55);
    background: linear-gradient(to bottom, rgba(111, 201, 193, 0.55) 0%, rgba(33, 61, 75, 0.55) 100%);
    -webkit-backdrop-filter: blur(4px);
    transition: all 0.3s cubic-bezier(0.15, 0.5, 0.5, 0.95) 0s; }
  .column.has-sizer .content {
    position: absolute;
    width: 90%;
    height: auto;
    top: 50%;
    left: 5%;
    transform: translateY(-50%);
    z-index: 1; }
  .column.has-sizer .sizer {
    width: 100%;
    height: auto; }

